import Vue from 'vue'
import Router from 'vue-router'
import ShouYe from "../components/View/ShouYe";
import FenLei from "../components/View/FenLei";
import DongTai from "../components/View/DongTai";
import WoDe from "../components/View/WoDe";
import Video from "../components/Video";
import CategoryVideo from "../components/CategoryVideo";
import History from "../components/HomeDate/History";
import AboutUs from "../components/HomeDate/AboutUs";
import Attention from "../components/HomeDate/Attention";
import Caching from "../components/HomeDate/Caching";
import Favorite from "../components/HomeDate/Favorite";
import Feedback from "../components/HomeDate/Feedback";
import News from "../components/HomeDate/News";
import Submit from "../components/HomeDate/Submit";
import UserDetails from "../components/UserDetails/UserDetails";
import UserFiles from "../components/UserDetails/UserFiles";
import VideoUpload from "../components/VideoUpload";
import SearchFor from "../components/HomeDate/SearchFor";



Vue.use(Router);

const router = new Router({
  routes: [
    {
      path: '/',
      redirect:{
        name:"ShouYe"
      }
    },{
      path:'/iconshouye',
      name:'ShouYe',
      component: ShouYe
    },{
      path:'/iconfenlei',
      name:'FenLei',
      component: FenLei
    },{
      path:'/iconxin',
      name:'DongTai',
      component: DongTai
    },{
      path:'/iconmy_icon',
      name:'WoDe',
      component: WoDe
    },{
      path:'/video',
      name:'Video',
      component: Video,
      meta: {hideTabbar:true}
    },{
      path:'/categoryvideo',
      name:'CategoryVideo',
      component: CategoryVideo,
      meta: {hideTabbar:true}
    },{
      path:'/iconlishi',
      name:'iconlishi',
      component: History,
      meta: {hideTabbar:true}
    },{
      path:'/iconshoucang',
      name:'iconshoucang',
      component: Favorite,
      meta: {hideTabbar:true}
    },{
      path:'/iconguanzhu',
      name:'iconguanzhu',
      component: Attention,
      meta: {hideTabbar:true}
    },{
      path:'/iconxiaoxi',
      name:'iconxiaoxi',
      component: News,
      meta: {hideTabbar:true}
    },{
      path:'/iconshangchuan',
      name:'iconshangchuan',
      component: Submit,
      meta: {hideTabbar:true}
    },{
      path:'/iconlajixiang',
      name:'iconlajixiang',
      component: Caching,
      meta: {hideTabbar:true}
    },{
      path:'/iconxiaolian',
      name:'iconxiaolian',
      component: Feedback,
      meta: {hideTabbar:true}
    },{
      path:'/iconguanyuwomen',
      name:'iconguanyuwomen',
      component: AboutUs,
      meta: {hideTabbar:true}
    },{
      path:'/userdetails',
      name:'UserDetails',
      component: UserDetails,
      meta: {hideTabbar:true}
    },{
      path:'/userfiles',
      name:'UserFiles',
      component: UserFiles,
      meta: {hideTabbar:true}
    },{
      path:'/videoupload',
      name:'VideoUpload',
      component: VideoUpload,
      meta: {hideTabbar:true}
    },{
      path:'/searchfor',
      name:'SearchFor',
      component: SearchFor,
      meta: {hideTabbar:true}
    }
  ]

});


export default router;



