<template>

  <div class="fullheight">
    <div class="header" style="height: 50px;text-align:center; border-bottom: 1px solid #eee;">
      <a href="javascript:void(0)" @click="$router.go(-1)" style="text-decoration:none;out-line: none;color: #000">
        <div style="font-size: 15px;line-height: 50px;margin-left: 20px;float: left;">取消</div>
      </a>
      <div style="font-size:15px;width:100px;float:left;line-height: 50px;margin-left: 25%;font-family: 'Adobe 黑体 Std R'">
        发布视频
      </div>
      <a href="javascript:void(0)" @click="$router.go(-1)" style="text-decoration:none;out-line: none;color: #000">
        <div style="font-size: 15px;line-height: 50px;margin-right: 20px;float: right;">发布</div>
      </a>
    </div>

    <div class="videouploaddata">
      <div style="height: 70px;">
        <div style="float: left;margin-left: 10px;">
          <input style="height: 50px;border: none;outline:none" v-model="feedbacktitle" placeholder="视频简介..."/>
        </div>
      </div>
      <div class="video">
        <div class="pic_img">
          <div class="pic_img_box">
            <el-upload class="avatar-uploader"
                       action="http://localhost:8080/static/video/"
                       name="path"
                       v-bind:show-file-list="false">
              <video v-if="videoForm.showVideoPath !='' && !videoFlag"
                     v-bind:src="videoForm.showVideoPath"
                     class="avatar video-avatar"
                     controls="controls">
              </video>
              <i v-else-if="videoForm.showVideoPath =='' && !videoFlag"
                 class="el-icon-plus avatar-uploader-icon"></i>
              <el-progress v-if="videoFlag == true"
                           type="circle"
                           v-bind:percentage="videoUploadPercent"
                           style="margin-top:7px;"></el-progress>
            </el-upload>
          </div>
        </div>
      </div>
      <div class="videotitle">
        <div style="height: 50px;">
          <div
            style="float: left;height: 50px;font-size: 15px;font-weight: bold;line-height: 50px;margin-left: 10px;color: #3a3a3a;">
            标题
          </div>
          <div style="float: left;margin-left: 20px;line-height: 50px;">
            <input style="height: 30px;border: none;width: 200px;" v-model="feedbacktitle"
                   placeholder="请输入视频标题(不少于6个字)"/>
          </div>
        </div>
      </div>
      <div class="videocategory">
        <div style="height: 50px;">
          <div
            style="float: left;height: 50px;font-size: 15px;font-weight: bold;line-height: 50px;margin-left: 10px;color: #3a3a3a;">
            分类
          </div>
          <div style="float: left;margin-left: 20px;line-height: 50px;">
            <input style="height: 30px;border: none;width: 200px;" v-model="feedbacktitle"
                   placeholder="选择视频分类"/>
          </div>
        </div>
      </div>
    </div>
  </div>

</template>

<script>
    import axios from 'axios'

    export default {
        name: "VideoUpload",
        data() {
            return {
                feedbacktitle: '',
                videoFlag: false,
                //是否显示进度条
                videoUploadPercent: "",
                //进度条的进度，
                isShowUploadVideo: false,
                //显示上传按钮
                videoForm: {
                    showVideoPath: ''
                }
            }
        },
        methods: {
            beforeUploadVideo(file) {
                var fileSize = file.size / 1024 / 1024 < 200;
                if (['video/mp4', 'video/ogg', 'video/flv', 'video/avi', 'video/wmv', 'video/rmvb', 'video/mov'].indexOf(file.type) == -1) {
                    layer.msg("请上传正确的视频格式");
                    return false;
                }
                if (!fileSize) {
                    layer.msg("视频大小不能超过200MB");
                    return false;
                }
                this.isShowUploadVideo = false;
            },
            //进度条
            uploadVideoProcess(event, file, fileList) {
                this.videoFlag = true;
                this.videoUploadPercent = file.percentage.toFixed(0) * 1;
            },
            //上传成功回调
            handleVideoSuccess(res, file) {
                this.isShowUploadVideo = true;
                this.videoFlag = false;
                this.videoUploadPercent = 0;

                //前台上传地址
                if (file.status == 'success' ) {
                   this.videoForm.showVideoPath = file.url;
                } else {
                    layer.msg("上传失败，请重新上传");
                }

                //后台上传地址
                // if (res.Code == 0) {
                //     this.videoForm.showVideoPath = res.Data;
                // } else {
                //     layer.msg(res.Message);
                // }
            }
        },
        created() {
        }
    }
</script>

<style scoped>

  .avatar-uploader .el-upload {
    border: 1px dashed #d9d9d9;
    border-radius: 6px;
    cursor: pointer;
    position: relative;
    overflow: hidden;
  }

  .avatar-uploader .el-upload:hover {
    border-color: #409EFF;
  }

  .avatar-uploader-icon {
    font-size: 28px;
    color: #8c939d;
    width: 126px;
    height: 126px;
    line-height: 126px;
    text-align: center;
  }

  .avatar {
    width: 178px;
    height: 178px;
    display: block;
  }

  .videotitle {
    border-bottom: 1px solid #eee;
  }

  .videocategory {
    border-bottom: 1px solid #eee;
  }

</style>



