<template>
  <div class="fullheight">
    <div class="header" style="height: 60px;text-align:center;background-color: #eeeeee">
      <a href="javascript:void(0)" @click="$router.go(-1)" style="text-decoration:none;out-line: none;color: #000">
        <i class='iconfont iconleft' style="font-size: 18px;line-height: 60px;margin-left: 20px;float: left;"></i>
      </a>
      <div style="font-size: 16px;width:100px;float:left;line-height: 60px;margin-left: 25%">
        {{$route.query.cellname}}
      </div>
    </div>
    <div class="attentionDate">
      <mt-navbar v-model="selected">
        <mt-tab-item id="1">评论</mt-tab-item>
        <mt-tab-item id="2">点赞</mt-tab-item>
      </mt-navbar>
      <mt-tab-container v-model="selected">
        <mt-tab-container-item id="1">
          <div v-for="(u,index) in commentlist">
            <div style="height: 85px;" class="attentiondatecss">
              <div style="line-height: 60px;margin-left: 10px;float: left;">
                <img v-lazy="u.useradress" class="userimage">
              </div>
              <div style="float: left;height: 70px;width: 50%;margin-left: 10px;">
                <div style="font-size:12px;margin-top: 12px;margin-bottom: 8px;color: #9a9a9a">{{u.username}}&nbsp;&nbsp;回复了我</div>
                <div style="font-size: 14px;color: #000;height: 20px;overflow: hidden;">{{u.commentcontent}}</div>
                <div style="font-size: 10px;color: #9a9a9a;height: 20px;overflow: hidden;">{{u.commenttime
                  |datetimeFormat}}
                </div>
              </div>
              <div style="float: right;margin-right: 10px;height: 70px;">
                <div class="commentstyle">
                  {{u.informationname}}
                </div>
              </div>
            </div>
          </div>
        </mt-tab-container-item>
        <mt-tab-container-item id="2">
          <div v-for="(u,index) in likelist">
            <div style="height: 85px;" class="attentiondatecss">
              <div style="line-height: 60px;margin-left: 10px;float: left;">
                <img v-lazy="u.useradress" class="userimage">
              </div>
              <div style="float: left;height: 70px;width: 50%;margin-left: 10px;">
                <div style="font-size:12px;margin-top: 12px;margin-bottom: 8px;color: #000">{{u.username}}&nbsp;&nbsp;赞了我</div>
                <div style="font-size: 10px;color: #9a9a9a;height: 20px;overflow: hidden;">{{u.commenttime
                  |datetimeFormat}}
                </div>
              </div>
              <div style="float: right;margin-right: 10px;height: 70px;">
                <div class="commentstyle">
                  <div style="margin-top: 3px;font-size: 10px;transform:scale(0.9);">
                    {{u.informationname}}
                  </div>
                </div>
              </div>
            </div>
          </div>
        </mt-tab-container-item>
      </mt-tab-container>
    </div>
  </div>
</template>

<script>
    import axios from "axios"

    export default {
        name: "News",
        data() {
            return {
                selected: "1",
                commentlist: [],
                likelist: []
            }
        },
        methods: {
            infoDate() {
                axios.get(`/video/getCommentList?userid=${this.$store.state.userid}`).then(resp => {
                    if (!resp.data.status) {
                        this.$alert(resp.data.errmsg, "提示");
                        return;
                    }
                    if (resp.data.nopermission) {
                        this.$alert("登录状态失效，请重新登录", "提示");
                        window.location.href = "/login.html";
                        return;
                    }
                    this.commentlist = Object.freeze(resp.data.data.commentlist);
                }).catch((err, data) => {
                    console.error(err);
                    this.$alert("请检查网络连接，如果完好请通知管理员服务器遇到错误，谢谢！", "提示");
                });
                axios.get(`/video/getLikeList?userid=${this.$store.state.userid}`).then(resp => {
                    if (!resp.data.status) {
                        this.$alert(resp.data.errmsg, "提示");
                        return;
                    }
                    if (resp.data.nopermission) {
                        this.$alert("登录状态失效，请重新登录", "提示");
                        window.location.href = "/login.html";
                        return;
                    }
                    this.likelist = Object.freeze(resp.data.data.likelist);
                }).catch((err, data) => {
                    console.error(err);
                    this.$alert("请检查网络连接，如果完好请通知管理员服务器遇到错误，谢谢！", "提示");
                });
            }
        },
        created() {
            this.infoDate();
        }
    }
</script>

<style scoped>
  .userimage {
    margin: 0 auto;
    display: inline-block;
    vertical-align: middle;
    width: 45px;
    height: 45px;
    border-radius: 50%;
  }

  .attentiondatecss {
    border-bottom: 1px solid #dcdcdc;
  }

  .commentstyle {
    background-color: #eeeeee;
    width: 75px;
    height: 45px;
    margin-right: 10px;
    margin-top: 20px;
    border-radius: 5px;
    font-size: 10px;
    color: #9e9e9e;
    overflow: hidden;
  }


</style>



