function datetimeFormat(t){
  let time = t?new Date(t):new Date();

  let hours = time.getHours();

  hours += "";
  let month = time.getMonth() + 1 + "";
  let tdate = time.getDate() + "";
  let minutes = time.getMinutes() + "";
  let seconds = time.getSeconds() + "";

  if(month.length === 1){
    month = "0" + month;
  }
  if(tdate.length === 1){
    tdate = "0" + tdate;
  }
  if(hours.length === 1){
    hours = "0" + hours;
  }
  if(minutes.length === 1){
    minutes = "0" + minutes;
  }
  if(seconds.length === 1){
    seconds = "0" + seconds;
  }

  return {
    date:`${time.getFullYear()}-${month}-${tdate}`,
    time:`${hours}:${minutes}`,
    sec:`:${seconds}`
  };
}

Vue.filter('dateFormat', function (t) {
  return datetimeFormat(t).date;
});

Vue.filter('timeFormat', function (t,sec) {
  let fobj = datetimeFormat(t);
  if(sec){
    return `${fobj.time}${fobj.sec}`;
  }
  return fobj.time;
});

Vue.filter('datetimeFormat', function (t,sec) {

  if (typeof(t)){
    t=parseInt(t);
    let fobj = datetimeFormat(t);
    if(sec){
      return `${fobj.date} ${fobj.time}${fobj.sec}`;
    }
    return `${fobj.date} ${fobj.time}`;
  }else {
    let fobj = datetimeFormat(t);
    if(sec){
      return `${fobj.date} ${fobj.time}${fobj.sec}`;
    }
    return `${fobj.date} ${fobj.time}`;
  }
});

Vue.filter('timedifftoday', function (t) {
  return (t / 86400000).toFixed(0);
});

Vue.filter("toThousands",function (num){
  num = parseFloat(num);
  if(isNaN(num)){
    return "";
  }
  num = num.toFixed(0);
  num = (num || 0).toString();
  let result = '';
  while (num.length > 3) {
    result = ',' + num.slice(-3) + result;
    num = num.slice(0, num.length - 3);
  }
  if (num) { result = num + result; }
  return result;
});

Vue.filter("fixed",function (num,c){
  if(typeof c == "undefined" || c == null){
    c = 1;
  }
  num = parseFloat(num);
  num = num.toFixed(c);
  return num;
});

Vue.filter("fixedp",function (num,c){
  if(typeof c == "undefined" || c == null){
    c = 1;
  }
  num = parseFloat(num);
  num = (num*100).toFixed(c);
  return num+"%";
});

Vue.filter("fromnow",function (t){
  let date = t?new Date(t):new Date();
  let now = new Date()
  let d = now.getTime() - date.getTime();

  if(d < 10000){
    return "刚刚";
  }
  if(d < 60000){
    return parseInt(d / 1000) + "秒前";
  }
  if (d < 3600000) {
    return parseInt(d / 60000) + "分钟前";
  }
  let hours = date.getHours() + "";
  if(hours.length == 1){
    hours = "0" + hours;
  }
  let minutes = date.getMinutes() + "";
  if (minutes.length == 1) {
    minutes = "0" + minutes;
  }
  if(date.getDate() == now.getDate()){
    return "今天" + hours + ":" + minutes;
  }
  if(date.getDate() == (now.getDate() - 1)){
    return "昨天" + hours + ":" + minutes;
  }
  let fobj = datetimeFormat(t);
  return `${fobj.date} ${fobj.time}${fobj.sec}`;
});


Vue.filter('secsformat', function (t) {
  let hours = 0;
  let minutes = 0;

  if(t > 3600){
    hours = parseInt(t / 3600) + "";
  }
  minutes = parseInt((t - (hours * 3600)) / 60) + "";

  if(minutes.length === 1){
    minutes = 0 + minutes;
  }
  return `${hours}:${minutes}`;
});

/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,store,
  components: { App },
  template: '<App/>'
});



