<template>
  <div class="fullheight">
    <div class="header" style="height: 50px;text-align:center;background-color: #eeeeee">
      <div style="float:left;line-height: 50px;width: 75%;margin-left: 20px;">
        <el-input
          height="50px"
          v-model="content">
          <i slot="prefix" class="el-input__icon el-icon-search" @click="searchvideofiles"></i>
        </el-input>
      </div>
      <a href="javascript:void(0)" @click="$router.go(-1)" style="text-decoration:none;out-line: none;color: #000">
        <div style="float: right;line-height: 50px;margin-right: 20px;font-size: 14px;">取消</div>
      </a>
    </div>

    <div v-for="item in videofilelists">
      <div @click="byvideoidandcategory(item.id,item.categoryId)" class="categoryvideolist"
           style="height: 100px;width: 100%;margin-top: 10px;border-bottom: 1px solid #dfdfdf">
        <img v-lazy="item.cover"
             style="width: 120px;margin-top: 10px;height: 80px;margin-left: 10px;border-radius: 5px;float: left;">
        <div style="margin-top: 10px;width: 200px;float: left;">
          <div style="font-weight: bold;overflow: hidden;height: 20px;margin-left: 10px;">{{item.videoTitle}}</div>
          <br>
          <div style="margin-left: 10px;color: #606060;">
            <span>{{item.uploadTime | datetimeFormat}}</span><br>
            <span>{{item.userName}}</span>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
    import axios from "axios"

    export default {
        name: "SearchFor",
        data() {
            return {
                content: '',
                videofilelists: []
            }
        },
        methods: {
            searchvideofiles() {
                axios.get(`/app/queryByLikeVideoName?content=${this.content}`).then(resp => {
                    this.videofilelists = Object.freeze(resp.data.data);
                }).catch((err, data) => {
                    console.error(err);
                    this.$alert("请检查网络连接，如果完好请通知管理员服务器遇到错误，谢谢！", "提示");
                });
            }
        }
    }
</script>

<style scoped>

</style>



