<template>
  <div class="test" @click="mycellwode">
    <i :class="[icon,'iconfont']" style="font-size: 17px;float: left;margin-left: 20px;margin-right: 10px;"></i>
    <div style="font-size: 16px;width: 50%;float: left;">{{name}}</div>
    <i class='iconfont iconiconfontyoujiantou-copy-copy-copy-copy-copy'
       style="font-size: 17px;float: right;margin-right: 20px;"></i>
  </div>
</template>

<script>
    export default {
        name: "MyCell",
        props: {
            icon: {
                type: String,
                default: ''
            },
            name: {
                type: String,
                default: ''
            }
        },
        data() {
            return {
                userid: this.$store.state.userid,
                cellname: this.name,
                mycellname: this.icon
            }
        },
        methods: {
            mycellwode() {
                console.log(this.icon);
                if (this.icon === "iconlajixiang") {
                    this.open();
                } else if (this.icon==="iconguanyuwomen"){
                    this.open1();
                } else {
                    this.$router.push({name: this.mycellname, query: {userid: this.userid, cellname: this.cellname}});
                }
            },
            open() {
                const h = this.$createElement;
                this.$msgbox({
                    title: '提示',
                    message: h('p', null, [
                        h('span', null, '您确定要清理全部缓存？ '),
                        h('i', { style: 'color: teal' }, null)
                    ]),
                    showClose:false,
                    closeOnClickModal:false,
                    showCancelButton: true,
                    confirmButtonText: '确定',
                }).catch(() => {
                });
            },
            open1(){
                const h = this.$createElement;
                this.$msgbox({
                    title: '提示',
                    message: h('p', null, [
                        h('span', null, '未开通？ '),
                        h('i', { style: 'color: teal' }, null)
                    ]),
                    showClose:false,
                    closeOnClickModal:false,
                    closeOnPressEscape:false,
                    showCancelButton: true,
                    confirmButtonText: '确定',
                }).catch(() => {
                });
            }
        }
    }
</script>

<style scoped>

</style>



