<template>
  <div class="fullheight">
    <div class="header" style="height: 60px;text-align:center;background-color: #eeeeee">
      <a href="javascript:void(0)" @click="$router.go(-1)" style="text-decoration:none;out-line: none;color: #000">
        <i class='iconfont iconleft' style="font-size: 18px;line-height: 60px;margin-left: 20px;float: left;"></i>
      </a>
      <div style="font-size: 16px;width:100px;float:left;line-height: 60px;margin-left: 25%">
        {{$route.query.cellname}}
      </div>
    </div>

    <div  v-if="submitlist.length!=0" v-for="u in submitlist">
      <div class="categoryvideolist"
           style="height: 100px;border-bottom: 1px solid #ccc;margin-left: 10px;">
        <div @click="byvideoidandcategory('3247685234','Android')">
          <img v-lazy="u.videoadress"
               style="width: 120px;margin-top: 10px;height: 80px;border-radius: 5px;float: left">
          <div style="margin-top: 10px;width: 160px;float: left;margin-left: 10px;">
            <div style="font-weight: bold;overflow: hidden;height: 45px;font-size: 14px;">
              {{u.videoname}}
            </div>
            <div>
              <div style="font-size: 13px;color: #666">{{u.submittime |datetimeFormat}}</div>
              <div style="font-size: 12px;color: #666">{{u.filesize}}M</div>
            </div>
          </div>
        </div>
        <div style="float: right;line-height: 45px;">
          <i class='iconfont iconmsnui-more' style="float: right;margin-right: 20px;color: #aaa;font-size: 16px;"
             @click="iconpopup(u.videoid)"></i>
        </div>
      </div>
    </div>
    <div v-if="submitlist.length==0" style="text-align: center;margin-top: 300px">
      <i class='iconfont iconwushuju' style="color: #999999;"> </i><br>
      <span style="color:#9a9a9a;">暂时没有数据~</span>
    </div>

    <mt-popup
      v-model="popupVisible"
      style="height: 180px;width: 100%"
      position="bottom">
      <div style="height: 180px;width: 100%;">
        <div @click="deletevideofile" style="height: 60px;border-bottom: 1px solid #ccc">
          <span style="line-height: 60px;margin-left: 20px">删除</span>
        </div>
        <div @click="unsubscribe" style="height: 60px;border-bottom: 1px solid #ccc">
          <span style="line-height: 60px;margin-left: 20px">修改视频信息</span>
        </div>
        <div @click="popupVisible=false" style="height: 60px;">
          <span style="line-height: 60px;margin-left: 20px">取消</span>
        </div>
      </div>
    </mt-popup>



  </div>
</template>

<script>
    import axios from "axios"

    export default {
        name: "Submit",
        data() {
            return {
                submitlist: [],
                popupVisible:false,
                videoid: ''
            }
        },
        methods: {
            byvideoidandcategory(videoid, category) {
                this.$router.push({name: 'Video', query: {videoid: videoid, category: category}});
            },
            iconpopup(videoid) {
                this.popupVisible = true;
                this.videoid = videoid;
            },
            unsubscribe() {
                this.popupVisible = false;
                this.$alert("获取信息表的主键，根据主键信息修改该条视频文件信息！！！" + this.videoid);
            },
            deletevideofile(){
                this.popupVisible = false;
                this.$alert("获取信息表的主键，根据主键信息删除该视频文件！！！" + this.videoid);
            },
            infoData() {
                axios.get(`/app/getFileList?userid=${this.$store.state.userid}`).then(resp => {
                    if (!resp.data.status) {
                        this.$alert(resp.data.errmsg, "提示");
                        return;
                    }
                    if (resp.data.nopermission) {
                        this.$alert("登录状态失效，请重新登录", "提示");
                        window.location.href = "/login.html";
                        return;
                    }
                    this.submitlist = Object.freeze(resp.data.data.submitlist);
                }).catch((err, data) => {
                    console.error(err);
                    this.$alert("请检查网络连接，如果完好请通知管理员服务器遇到错误，谢谢！", "提示");
                });
            }
        },
        created() {
            this.infoData();
        }
    }
</script>

<style scoped>

  .fullheight {
    height: 820px;
    background-color: #eeeeee;
  }

  .attentiondatecss {
    border-bottom: 1px solid #dcdcdc;
  }

  .userimage {
    margin: 0 auto;
    display: inline-block;
    vertical-align: middle;
    width: 45px;
    height: 45px;
    border-radius: 50%;
  }


</style>



