<template>

  <div class="fullheight">
    <div class="header" style="height: 50px">
      <a href="javascript:void(0)" @click="$router.go(-1)" style="text-decoration:none;out-line: none;color: #000">
        <i class='iconfont iconleft' style="font-size: 18px;line-height: 50px;margin-left: 20px;float: left"></i>
      </a>
      <div style="font-size: 15px;width:100px;float:left;;line-height: 50px;text-align: center;overflow: hidden;margin-left: 30%;">
        {{$route.query.category.cateName}}
      </div>
    </div>
    <div v-for="item in categorylists">
      <div @click="byvideoidandcategory(item.id,item.categoryId)" class="categoryvideolist"
           style="height: 100px;width: 100%;margin-top: 10px;border-bottom: 1px solid #ccc">
        <img v-lazy="item.cover"
             style="width: 120px;margin-top: 10px;height: 80px;margin-left: 10px;border-radius: 5px;float: left;">
        <div style="margin-top: 10px;width: 200px;float: left">
          <div style="font-weight: bold;overflow: hidden;height: 20px;margin-left: 10px;">{{item.videoTitle}}</div>
          <br>
          <span>{{item.uploadTime | datetimeFormat}}</span><br>
          <span>{{item.userName}}</span>
        </div>
      </div>
    </div>

  </div>

</template>

<script>
    import axios from 'axios'

    export default {
        name: "CategoryVideo",
        // provide() {
        //     return {
        //         reload: this.reload
        //     };
        // },
        data() {
            return {
                selected: '1',
                categoryData: '',
                categorylists: []
            }
        },
        methods: {
            infoDate() {
                axios.get(`/app/queryCateVideoList?cateId=${this.categoryData.id}`).then(resp => {
                    // if (!resp.data.status) {
                    //     this.$alert(resp.data.errmsg, "提示");
                    //     return;
                    // }
                    // if (resp.data.nopermission) {
                    //     this.$alert("登录状态失效，请重新登录", "提示");
                    //     window.location.href = "/login.html";
                    //     return;
                    // }
                    this.categorylists = Object.freeze(resp.data.data);
                }).catch((err, data) => {
                    console.error(err);
                    this.$alert("请检查网络连接，如果完好请通知管理员服务器遇到错误，谢谢！", "提示");
                });
            },
            byvideoidandcategory(videoid, categoryid) {
                this.$router.push({name: 'Video', query: {videoid: videoid, categoryid: categoryid}});
            },
            reload() {
                this.isRouterAlive = false;
                this.$nextTick(function() {
                    this.isRouterAlive = true;
                });
            }
        },
        created() {
            this.categoryData=this.$route.query.category;
            this.infoDate();
        }
    }
</script>

<style scoped>
  .fullheight {
    height: 820px;
    background-color: #eeeeee;
  }

  .categoryvideolist span {
    margin-left: 10px;
  }

</style>



