<template>
  <div id="app">
    <router-view/>
    <mt-tabbar v-model="selected" v-if="!hide" style="background-color: #eeeeee">
      <mt-tab-item v-for="item in tabBar" :id="item.id" @click.native="switchtab(item.id)">
        <i :class=tabItemImage(item.id) style="font-size: 20px"></i>
        <br>
        <span>{{ item.lable }}</span>
      </mt-tab-item>
    </mt-tabbar>
  </div>

</template>

<script>
    export default {
        name: 'App',
        data() {
            return {
                selected: 'iconshouye',
                showc: '',
                tabBar: [
                    {id: 'iconshouye', lable: '首页'},
                    {id: 'iconfenlei', lable: '分类'},
                    {id: 'iconxin', lable: '动态'},
                    {id: 'iconmy_icon', lable: '我的'},
                ]
            }
        },
        computed: {
            hide() {
                return this.$route.meta.hideTabbar
            }
        },
        methods: {
            tabItemImage(itemID) {
                return (`iconfont ${itemID}`)
            },
            switchtab(tabid) {
                this.$router.replace({path: `/${tabid}`});
            }
        }
    }
</script>


<style>
  body {
    margin: 0px;
    padding: 0px;
  }

  #app{
    height: 100%;
    width: 100%;
  }

  .mint-tab-item-label {
    font-size: 16px;
    height: 100%;
    line-height: 1.4;
  }

  .iconfont {
    font-size: 20px;
  }

  .el-main-nf {
    overflow-y: scroll;
  }

  .mint-tabbar{
    position: fixed;
    left: 0px;
    bottom: 0px;
    width: 100%;
    height: 60px;
    background-color: #eee;
    z-index: 9999;
  }

  .mint-tabbar > .mint-tab-item.is-selected {
    color: #a15559;
  }

  .mint-navbar .mint-tab-item.is-selected {
    color: #a15559;
    border-bottom: 3px solid #a15559;
  }

  .el-tabs__item:hover {
    color: #cccccc;
  }


  .el-tabs__item.is-active {
    color: #000000;
    background-color: #d9d9d9;
  }

  .el-tabs__active-bar {
    background-color: #f1f1f1;
  }

  .el-tabs--left .el-tabs__header.is-left {
    margin-right: 0px;
  }

  .el-tabs--left .el-tabs__item.is-left {
    text-align: center;
  }

  .el-tabs__item {
    border-bottom: 1px solid #f1f1f1;
  }

  .el-tabs__nav-wrap::after {
    background-color: #f1f1f1;
  }

  .el-checkbox__inner {
    width: 25px;
    height: 25px;
    border-radius: 50%;
    border: 1px solid #aaa;
    background-color: #eeeeee;
  }

  .el-checkbox-group {
    float: left;
    width: 30px;
    line-height: 100px;
    margin-right: 10px;
  }

  .el-checkbox__inner::after {
    height: 15px;
    left: 7px;
    top: 0px;
    width: 8px;
  }

  .el-checkbox__input.is-indeterminate .el-checkbox__inner::before {
    top: 10px;
  }

  .el-checkbox__label {
    font-size: 16px;
    display: none;
  }

  .el-checkbox__input.is-checked + .el-checkbox__label {
    display: none;
  }

  .mint-tab-container-wrap {
    margin-top: 10px;
  }

  .el-message-box {
    width: 70%;
  }

  .mint-button--danger {
    background-color: #ab2e35;
  }

  .el-form-item {
    margin-bottom: 0px;
    height: 150px;
    width: 150px;
    background-color: #eee;
    margin-left: 10px;
  }


  .el-upload{
    background-color: #eee;
    margin-left: 10px;
  }

  /*.el-form-item__label{*/
  /*  display: none;*/
  /*}*/

  /*.el-icon-plus:before{*/
  /*  font-size: 25px;*/
  /*  line-height: 150px;*/
  /*  margin-left: 50px;*/
  /*}*/

  .el-input__prefix{
    line-height: 55px;
  }

  .el-input__icon{
    color: #000;
    font-size: 20px;
  }

  .el-input--prefix .el-input__inner{
    border-radius: 30px;
    border: none;
    height: 32px;
    padding-left: 40px;
  }


</style>



