<template>
  <div class="fullheight">
    <div class="header" style="height: 60px;text-align:center;">
      <a href="javascript:void(0)" @click="$router.go(-1)" style="text-decoration:none;out-line: none;color: #000">
        <i class='iconfont iconleft' style="font-size: 18px;line-height: 60px;margin-left: 20px;float: left;"></i>
      </a>
      <div style="font-size: 16px;width:100px;float:left;line-height: 60px;margin-left: 25%;color: #323232;">
        {{$route.query.username}}
      </div>
    </div>

    <div class="userdate">
      <div class="cellheader">
        <div style="height: 85px;border-radius: 50%;float: left;line-height: 85px;">
          <img style="line-height: 85px;margin: 0 auto;display: inline-block; vertical-align: middle;"
               v-lazy="userDetails.useradress" class="userimage">
        </div>
        <div style="float: left;margin-top: 25px;margin-left: 20px">
          <div style="font-size: 14px;color: #881c22;font-weight: bold;">{{userDetails.username}}</div>
          <div style="font-size: 12px;margin-top: 5px;">{{userDetails.introduction}}</div>
        </div>
        <div style="float: right;margin-right: 20px;width: 60px;height: 70px;">
          <div>
            <div @click="fanclick(userDetails,$event)" v-if="userDetails.mutualpowder==='1'" class="test">
              已关注
            </div>
            <div @click="fanclick(userDetails,$event)" v-if="userDetails.mutualpowder==='0'" class="test1">
              关注
            </div>
          </div>
        </div>
      </div>
      <div style="height: 40px;margin-left: 10px;">
        <div style="font-size: 12px;color: #a0a0a0;line-height: 40px;height: 40px;width:60px;float: left;">
          <div style="float: left;margin-right: 5px;">{{userDetails.followers}}</div>
          <div style="float: left;">关注</div>
        </div>
        <div style="font-size: 12px;color: #a0a0a0;line-height: 40px;height: 40px;float: left;">
          <div style="float: left;margin-right: 5px;">{{userDetails.numberoffans}}</div>
          <div style="float: left;">粉丝</div>
        </div>
      </div>


    </div>
    <div v-for="u in userfiles">
      <div class="categoryvideolist"
           style="height: 95px;border-bottom: 1px solid #f2f2f2;margin-left: 10px;">
        <div @click="byvideoidandcategory('3247685234','Android')">
          <img v-lazy="u.videoadress"
               style="width: 120px;height: 75px;border-radius: 5px;float: left;margin-top: 10px;">
          <div style="margin-top: 13px;width: 160px;float: left;font-size: 12px;margin-left: 10px;">
            <div style="font-weight: bold;overflow: hidden;height: 38px;font-size: 14px;">{{u.videoname}}</div>
            <span>{{u.time |datetimeFormat}}</span><br>
            <span>播放{{u.views}}次</span>
          </div>
        </div>

      </div>
    </div>


  </div>
</template>

<script>
    import axios from "axios"

    export default {
        name: "UserDetails",
        data() {
            return {
                userDetails: {
                    userid: '',
                    username: '',
                    introduction: '',
                    followers: '',
                    mutualpowder: '',
                    numberoffans: '',
                    useradress: ''
                },
                userfiles: []
            }
        },
        methods: {
            fanclick(val, event) {
                let dom = event.target;
                if (dom.innerHTML == "关注") {
                    this.$nextTick(() => {
                        dom.innerHTML = '已关注';
                        dom.className = "test";
                    });
                } else {
                    this.$nextTick(() => {
                        dom.innerHTML = '关注';
                        dom.className = "test1";
                    });
                }
            },
            byvideoidandcategory(videoid,category){
                this.$router.push({name: 'Video', query: {videoid: videoid, category: category}});
            },
            infoData() {
                axios.get(`/video/getUserFiles?userid=${this.$store.state.userid}`).then(resp => {
                    if (!resp.data.status) {
                        this.$alert(resp.data.errmsg, "提示");
                        return;
                    }
                    if (resp.data.nopermission) {
                        this.$alert("登录状态失效，请重新登录", "提示");
                        window.location.href = "/login.html";
                        return;
                    }
                    this.userDetails = Object.freeze(resp.data.data.userDetails);
                    this.userfiles = Object.freeze(resp.data.data.userfiles);
                }).catch((err, data) => {
                    console.error(err);
                    this.$alert("请检查网络连接，如果完好请通知管理员服务器遇到错误，谢谢！", "提示");
                });
            }
        },
        created() {
            this.infoData();
        }
    }
</script>

<style scoped>

  .header {
    background-color: #eee;
  }

  .userdate {
    border-bottom: 1px solid #f2f2f2;
  }

  .userimage {
    width: 63px;
    height: 63px;
    border-radius: 50%;
    line-height: 85px;
    margin: 0 auto;
    display: inline-block;
    vertical-align: middle;
  }

  .cellheader {
    height: 85px;
    color: #666666;
    border-bottom: 1px solid #fbfbfb;
    margin-left: 10px;
  }

  .test {
    width: 55px;
    height: 25px;
    background-color: #ccc;
    color: #fff;
    font-size: 12px;
    text-align: center;
    line-height: 25px;
    border-radius: 2px;
    margin-top: 22.5px;
  }

  .test1 {
    width: 55px;
    height: 25px;
    background-color: #ab2e35;
    color: #fff;
    font-size: 12px;
    text-align: center;
    line-height: 25px;
    border-radius: 2px;
    margin-top: 22.5px;
  }

  .categoryvideolist span{
    color: #666666;
  }


</style>



