<template>

  <div class="fullheight">

    <div class="header">
      <span>动态</span>
    </div>
    <div style="overflow: auto;height: 700px">

      <div style="background-color: #fff;margin-bottom: 10px;" v-for="u in videolist">
        <div style="height: 90px;">
          <div style="height: 60px;float: left;" @click="infovideo(u)">
            <div style="height: 60px;width: 70px;float: left;text-align: center;">
              <img v-lazy="u.cover" class="userimagestyle">
            </div>
            <div style="float: left;width: 200px;height: 60px;">
              <div style="margin-top: 10px;color: #881d23;margin-bottom: 5px;">{{u.userName}}</div>
              <div style="float: left;font-size: 12px;color: #aaa">{{u.uploadTime |datetimeFormat}}</div>
            </div>
          </div>
          <div style="float: right;line-height: 60px;">
            <i class='iconfont iconmsnui-more' style="float: right;margin-right: 30px;"
               @click="iconpopup(u)"></i>
          </div>
          <div style="width: 50%;overflow: hidden;font-size: 12px;margin-left: 20px;color: #666">
            {{u.description}}
          </div>
        </div>
        <div @click="infovideo(u)">
          <img v-lazy="u.cover" class="videoimagestyle">
          <div style="margin-left: 20px;font-weight: bold;height: 50px;">{{u.videoTitle}}</div>
        </div>
      </div>

      <mt-popup
        v-model="popupVisible"
        style="height: 120px;width: 100%"
        position="bottom">
        <div style="height: 120px;width: 100%;">
          <div @click="unsubscribe" style="height: 60px;border-bottom: 1px solid #ccc">
            <span style="line-height: 60px;margin-left: 20px">取消关注</span>
          </div>
          <div @click="popupVisible=false" style="height: 60px;">
            <span style="line-height: 60px;margin-left: 20px">取消</span>
          </div>
        </div>
      </mt-popup>
    </div>

  </div>
</template>

<script>
    import axios from "axios"

    export default {
        name: "DongTai",
        data() {
            return {
                videolist: [],
                popupVisible: false,
                userid: this.$store.state.userid,
                attentionid: ''
            }
        },
        methods: {
            infoData() {
                axios.get(`/app/queryByUserMoving?userId=${this.$store.state.userid}`).then(resp => {
                    // if (!resp.data.status) {
                    //     this.$alert(resp.data.errmsg, "提示");
                    //     return;
                    // }
                    // if (resp.data.nopermission) {
                    //     this.$alert("登录状态失效，请重新登录", "提示");
                    //     window.location.href = "/login.html";
                    //     return;
                    // }
                    this.videolist = Object.freeze(resp.data.data);
                }).catch((err, data) => {
                    console.error(err);
                    this.$alert("请检查网络连接，如果完好请通知管理员服务器遇到错误，谢谢！", "提示");
                });
            },
            iconpopup(attentionid) {
                this.popupVisible = true;
                this.attentionid = attentionid;
            },
            unsubscribe() {
                this.popupVisible = false;
                console.log(this.userid);
                console.log(this.attentionid);
            },
            infovideo(video) {
                this.$router.push({name: 'Video', query: {videoid: video.videoid, category: video.category}});
            }
        },
        created() {
            this.infoData();
        }
    }
</script>

<style scoped>
  .fullheight {
    height: 820px;
    background-color: #eeeeee;
  }


  .header {
    height: 50px;
    text-align: center;
    line-height: 50px;
    font-size: 16px;
  }

  .userimagestyle {
    width: 40px;
    height: 40px;
    border-radius: 50%;
    margin-top: 10px;
  }

  .videoimagestyle {
    height: 200px;
    width: 89%;
    margin-left: 20px;
    margin-right: 20px;
    border-radius: 5px;
  }


</style>



