<template>
  <div class="fullheight">
    <div class="header" style="height: 60px;text-align:center;">
      <a href="javascript:void(0)" @click="$router.go(-1)" style="text-decoration:none;out-line: none;color: #000">
        <i class='iconfont iconleft' style="font-size: 18px;line-height: 60px;margin-left: 20px;float: left;"></i>
      </a>
      <div style="font-size: 16px;width:100px;float:left;line-height: 60px;margin-left: 25%">
        {{$route.query.cellname}}
      </div>
      <i @click="userdropout" class='iconfont icontuichu'
         style="font-size: 18px;line-height: 60px;margin-right: 20px;float: right;"></i>
    </div>

    <div class="userdate">
      <div class="cellheader">
        <div style="font-size: 14px;float: left;line-height: 90px;margin-left: 20px;">头像</div>
        <div style="float: right;line-height: 90px;margin-right: 20px;">
          <img style="margin: 0 auto;display: inline-block; vertical-align: middle;"
               v-lazy="userDetails.useradress" class="userimage">
        </div>
      </div>
      <div class="celldate">
        <div class="celltitle">用户名</div>
        <div style="float: right;line-height: 45px;margin-right: 20px;">
          <input style="border: none;outline:none;width: 150px;text-align:right;color: #666;font-size: 15px;"
                 v-model="userDetails.username"/>
        </div>
      </div>
      <div class="celldate">
        <div class="celltitle">简介</div>
        <div style="float: right;line-height: 45px;margin-right: 20px;">
          <input style="border: none;outline:none;width: 150px;text-align:right;color: #666;font-size: 15px;"
                 v-model="userDetails.introduction"/>
        </div>
      </div>
      <div class="celldate">
        <div class="celltitle">已绑定手机号码</div>
        <div style="float: right;line-height: 45px;margin-right: 20px;font-size: 14px;">
          {{userDetails.bindphone}}
        </div>
      </div>
      <div class="celldate">
        <div class="celltitle">已绑定三方账号</div>
        <div style="float: right;line-height: 45px;margin-right: 20px;">
          <i class='iconfont iconlogo-wechat' style="font-size: 30px;line-height: 45px;color: #c3c3c3;"></i>
        </div>
      </div>

    </div>

    <div style="text-align: center;line-height: 90px;">
      <mt-button style="width: 95%" type="danger">保存</mt-button>
    </div>

    <mt-popup
      v-model="popupVisible"
      style="height: 180px;width: 300px;border-radius: 5px;">
      <div style="height: 180px;width: 300px;">
        <div style="height: 70px;margin-left: 20px;line-height: 70px;">
          <span style="font-weight: bold;">提示</span>
        </div>
        <div style="height: 40px;color: #727882;margin-left: 20px;">
          您确定要退出登录？
        </div>
        <div @click="popupVisible=false" style="height: 70px;line-height: 70px;">
          <div style="color: #1f8aee;float: left;width: 50%;text-align: center;font-size: 14px;">
            取消
          </div>
          <div @click="popupVisible=false"
               style="color: #1f8aee;float: right;width: 50%;text-align: center;font-size: 14px;">
            确定
          </div>
        </div>
      </div>
    </mt-popup>


  </div>
</template>

<script>
    import axios from "axios"

    export default {
        name: "UserDetails",
        data() {
            return {
                userDetails: {
                    userid:'',
                    username:'',
                    introduction:'',
                    bindphone:'',
                    useradress:''
                },
                popupVisible: false
            }
        },
        methods: {
            userdropout() {
                this.popupVisible = true;
            },
            infoData() {
                axios.get(`/user/getUserDetails?userid=${this.$store.state.userid}`).then(resp => {
                    if (!resp.data.status) {
                        this.$alert(resp.data.errmsg, "提示");
                        return;
                    }
                    if (resp.data.nopermission) {
                        this.$alert("登录状态失效，请重新登录", "提示");
                        window.location.href = "/login.html";
                        return;
                    }
                    this.userDetails = Object.freeze(resp.data.data.userDetails);
                }).catch((err, data) => {
                    console.error(err);
                    this.$alert("请检查网络连接，如果完好请通知管理员服务器遇到错误，谢谢！", "提示");
                });
            }
        },
        created() {
            this.infoData();
        }
    }
</script>

<style scoped>

  .fullheight {
    height: 820px;
    background-color: #f9f9f9;
  }

  .header {
    background-color: #fff;
  }

  .userimage {
    width: 53px;
    height: 53px;
    border-radius: 50%;
  }

  .userdate {
    height: 275px;
    background-color: #fff;
    margin-top: 10px;
  }

  .cellheader {
    height: 90px;
    color: #666666;
    border-bottom: 1px solid #fbfbfb;
  }

  .celldate {
    height: 45px;
    color: #666;
    border-bottom: 1px solid #fbfbfb;
  }

  .celltitle {
    font-size: 14px;
    float: left;
    margin-left: 20px;
    line-height: 45px;
  }


</style>



