<template>
  <div class="fullheight">
    <div class="header" style="height: 60px;text-align:center;background-color: #eeeeee">
      <a href="javascript:void(0)" @click="$router.go(-1)" style="text-decoration:none;out-line: none;color: #000">
        <i class='iconfont iconleft' style="font-size: 18px;line-height: 60px;margin-left: 20px;float: left;"></i>
      </a>
      <div style="font-size: 16px;width:100px;float:left;line-height: 60px;margin-left: 25%">
        {{$route.query.cellname}}
      </div>
    </div>

    <div>
      <div style="height: 70px;">
        <div style="float: left;margin-left: 10px;height: 50px;line-height: 50px;">标题</div>
        <div style="float: left;margin-left: 20px;">
          <input style="height: 50px;background-color: #eeeeee;border: none;outline:none" v-model="feedbacktitle" placeholder="请输入反馈标题" />
        </div>
      </div>
      <div style="height: 70px;">
        <div style="float: left;margin-left: 10px;height: 50px;line-height: 40px;">内容</div>
        <div style="float: left;margin-left: 30px;">
          <input style="height: 50px;background-color: #eeeeee;border: none;outline:none" v-model="feedbackcontent" placeholder="请输入您宝贵的意见和建议" />
        </div>
      </div>
    </div>
    <div style="text-align: center;line-height: 320px;">
      <mt-button style="width: 95%" type="danger">提交</mt-button>
    </div>



  </div>
</template>

<script>
    export default {
        name: "Feedback",
        data(){
            return {
                feedbacktitle:"",
                feedbackcontent:''
            }
        }
    }
</script>

<style scoped>
  .fullheight {
    height: 820px;
    background-color: #eeeeee;
  }

  .attentiondatecss {
    border-bottom: 1px solid #dcdcdc;
  }
</style>



