<template>

  <div class="fullheight">

    <div class="header">
      <i @click="openSearch" class="iconfont iconsousuo" style="float: left;line-height: 50px;margin-left: 40px;"></i>
      <i @click="addProduct" class="iconfont icontianjia" style="float: right;line-height: 50px;margin-right: 40px"></i>
      <div>
        <span>首页</span>
      </div>
    </div>
    <div class="content" v-for="item in videolist" style="float: left;height: 180px">
        <div @click="infovideo(item)">
          <img v-lazy="item.cover" class="videoimage">
          <div class="test">
            {{item.videoTitle}}
          </div>
        </div>

      <div style="height: 30px;line-height: 30px">
        <span style="margin-left: 20px">{{item.cateName}}</span>
        <i class='iconfont icondianzan' style="font-size: 20px;float: right;margin-right: 20px">
          <span style="margin-left: 3px;font-size: 12px">{{item.likeCount}}</span>
        </i>
      </div>

    </div>
    <br style="float: none">

  </div>
</template>

<script>
    import axios from 'axios'

    export default {
        name: "ShouYe",
        data() {
            return {
                videolist: [],
                image: ''
            }
        },
        methods: {
            openSearch() {
                this.$router.push({name:'SearchFor'});
            },
            addProduct() {
                this.$router.push({name:'VideoUpload'});
            },
            infoData() {
                axios.get('/app/queryByReleaseTime').then(resp => {
                    // if (!resp.data.status) {
                    //     this.$alert(resp.data.errmsg, "提示");
                    //     return;
                    // }
                    // if (resp.data.nopermission) {
                    //     this.$alert("登录状态失效，请重新登录", "提示");
                    //     window.location.href = "/login.html";
                    //     return;
                    // }
                    this.videolist = Object.freeze(resp.data.data);
                }).catch((err, data) => {
                    console.error(err);
                    this.$alert("请检查网络连接，如果完好请通知管理员服务器遇到错误，谢谢！", "提示");
                });
            },
            infovideo(video) {
                this.$router.push({name:'Video',query: {videoid:video.id,categoryid:video.categoryId,userid:video.userId}});
            }

        },
        created() {
            this.infoData();
        }
    }
</script>

<style scoped>
  .header {
    border: none;
    background-color: #eee;
    height: 50px;
    width: 100%;
    margin-bottom: 8px;
  }

  .content div {
    font-size: 12px;
  }

  .header div {
    text-align: center;
    line-height: 50px;
    font-size: 20px;
  }

  .iconfont {
    font-size: 25px;
  }

  .content {
    width: 45%;
    height: 180px;
    margin-right: 2px;
    margin-left: 10px;
    margin-bottom: 5px;
    border-radius: 5px;
    border: 1px solid #eee;
  }

  .test {
    margin-left: 20px;
    overflow: hidden;
    height: 32px;
    font-weight: bold;
    margin-top: 8px;
    margin-right: 20px;
  }

  .videoimage {
    width: 100%;
    height: 100px;
    border-radius: 5px 5px 0px 0px;
  }


</style>



