<template>

  <div class="fullheight">

    <div>
      <div class="header" style="position:absolute;width: 100%;z-index:999;color: #fff;
background:linear-gradient(rgba(0,0,0,1),rgba(0,0,0,0));">
        <a href="javascript:void(0)" @click="$router.go(-1)" style="text-decoration:none;out-line: none;color:white">
          <i class='iconfont iconleft'
             style="font-size: 18px;line-height: 35px;margin-left: 20px;"></i>
        </a>
        <span style="color: #fff;line-height: 35px;margin-left: 10px;">{{videofile.videoTitle}}</span>
      </div>
      <div class="video-box" style="position:relative;z-index:1;height:200px">
        <video :src="videofile.path" controls controlsList='nofullscreen'
               ref="video"
               style="width: 100%;height: 100%;object-fit: fill;" ></video>
      </div>
    </div>
    <mt-navbar v-model="selected">
      <mt-tab-item id="1">简介</mt-tab-item>
      <mt-tab-item id="2">评论(2)</mt-tab-item>
    </mt-navbar>
    <mt-tab-container v-model="selected" style="overflow: auto;height: 600px;">
      <mt-tab-container-item id="1">
        <div style="height: 50px;width: 100%;" @click="openuserfiles">
          <div
            style="height: 50px;border-radius: 50%;float: left;line-height: 48px;width: 50px;text-align: center;margin-left: 20px;">
            <!--            videofile.userPicImg-->
            <img v-lazy="'../static/images/03.jpg'" class="userimage">
          </div>
          <div style="height: 60px;float: left;margin-left: 10px">
            <div style="font-size: 16px;color: #a15559">{{videofile.userName}}</div>
            <div style="font-size: 12px;">{{videofile.uploadTime | datetimeFormat}}</div>
          </div>
          <div style="float: right;margin-right: 20px;line-height: 50px">
            <mt-button size="normal" type="danger">
              <i class='iconfont iconjiahao' style="font-size: 5px"></i>
              <span>{{guanzhu}}</span>
            </mt-button>
          </div>
        </div>

        <div style="height: 60px;width: 100%;margin-left: 20px;float: left;">
          <div style="float: left">
            <div style="font-weight: bold;font-size: 14px">{{videofile.videoTitle}}</div>
            <div style="font-size: 12px;color: #303133f0;">{{videofile.description}}</div>
          </div>
        </div>

        <div style="height:15px;font-size: 12px;color: #cccccc;float: left;margin-left: 20px;">
          <span style="margin-right: 30px">播放 {{videofile.playCount}}次</span>
          <span>点赞 {{videofile.likeCount}}次</span>
        </div>
        <br>
        <div style="height: 60px;width: 100%;float: left;border-bottom: 1px solid #ccc;margin-top: 10px;">
          <div class="icons">
            <div>
              <i class='iconfont icondianzan' style="font-size: 25px;"></i>
            </div>
            <div>点赞</div>
          </div>
          <div class="icons">
            <div>
              <i class='iconfont iconbuxihuan' style=""></i>
            </div>
            <div>不喜欢</div>
          </div>
          <div class="icons">
            <div>
              <i class='iconfont iconshoucang' style=""></i>
            </div>
            <div>收藏</div>
          </div>
          <div class="icons">
            <div>
              <i class='iconfont iconfenxiang' style=""></i>
            </div>
            <div>分享</div>
          </div>
        </div>

        <div style="clear: both;"></div>

        <div style="color: #aaa;">
          <div v-if="videoList.length==0" style="margin-top: 100px;text-align: center;">
            <i class='iconfont iconleibie' style="font-size: 50px;color: #aaa;"></i>
            <br>
            <span>暂时没有数据</span>
          </div>
          <div v-if="videoList.length!=0" v-for="u in videoList" style="height: 105px;border-bottom: 1px solid #ccc;"
               @click="infovideo(u)">
            <img v-lazy="u.cover"
                 style="border-radius:5px;height: 80px;width: 130px;float: left;line-height: 105px;margin-left: 10px;margin-top: 12px;">
            <div style="margin-left: 10px;width: 200px;float: left;margin-top: 12px;color: #000;font-weight: bold;">
              {{u.videoTitle}}
            </div>
            <div style="margin-left: 10px;width: 220px;height: 50px;float: left;color: #6d6d6d;">{{u.description}}
            </div>
          </div>
        </div>
      </mt-tab-container-item>
      <mt-tab-container-item id="2">
        <!--        <div style="height: 90px;width: 100%;border-bottom: 1px solid #aaa;" v-for="u in commentlist">-->
        <!--          <div style="height: 60px;border-radius: 50%;margin-top: 20px;float: left">-->
        <!--            <img v-lazy="u.useradress" class="userimage">-->
        <!--          </div>-->
        <!--          <div style="height: 60px;float: left;margin-top: 15px;margin-left: 15px">-->
        <!--            <div style="font-size: 18px;color: #a15559;">{{u.username}}</div>-->
        <!--            <div style="font-size: 12px;">{{u.time |datetimeFormat}}</div>-->
        <!--            <div>{{u.content}}</div>-->
        <!--          </div>-->
        <!--        </div>-->
      </mt-tab-container-item>
    </mt-tab-container>
  </div>

</template>

<script>
    import axios from 'axios'

    export default {
        name: "Video",
        data() {
            return {
                selected: '1',
                guanzhu: '关注',
                videoList: [],
                videofile: {}
            }
        },
        methods: {
            infoData() {
                axios.get(`/app/queryByVideoDetail?videoId=${this.$route.query.videoid}&cateId=${this.$route.query.categoryid}&userId=${this.$route.query.userid}`).then(resp => {
                    // if (!resp.data.status) {
                    //     this.$alert(resp.data.errmsg, "提示");
                    //     return;
                    // }
                    // if (resp.data.nopermission) {
                    //     this.$alert("登录状态失效，请重新登录", "提示");
                    //     window.location.href = "/login.html";
                    //     return;
                    // }
                    this.videofile = Object.freeze(resp.data.data);
                    this.videoList = Object.freeze(resp.data.data.videoList);
                }).catch((err, data) => {
                    console.error(err);
                    this.$alert("请检查网络连接，如果完好请通知管理员服务器遇到错误，谢谢！", "提示");
                });

            },
            guanzhuclick() {
                if (this.guanzhu === '关注') {
                    this.guanzhu = "已关注";
                } else {
                    this.guanzhu = "关注";
                }
            },
            // infovideo(u) {
            //     // this.$router.push({name:'Video',query: {videoid:video.id,category:video.category}});
            //     this.$router.push({name: 'Video', query: {videoid: '213764521', category: 'Android'}});
            // },
            openuserfiles() {
                this.$router.push({
                    name: 'UserFiles',
                    query: {userid: this.$store.state.userid, username: '16619883314'}
                });
            }
        },
        created() {
            this.infoData();

        }
    }
</script>

<style scoped>

  .userimage {
    border-radius: 50%;
    height: 45px;
    width: 45px;
    vertical-align: middle;
  }

  .mint-button--danger {
    background-color: #ab2e35;
  }

  .mint-button {
    font-size: 12px;
    height: 30px;
    width: 68px;
  }

  .mint-button :active {
    background-color: #aaaaaa;
  }

  .icons {
    float: left;
    height: 60px;
    width: 25%;
    text-align: center;
  }

  .icons div {
    font-size: 12px !important;
    height: 25px;
    line-height: 25px;
  }


</style>



