<template>

  <div class="fullheight">
    <div class="header">
      <div>分类</div>
    </div>
    <div class="leftNave">
      <el-tabs :tab-position="tabPosition" style="height:100%;width:100%; ">
        <el-tab-pane v-for="(item,index) in catalogList" :label="item.cateName">
          <div class="catalog" v-for="u in item.categoryList">
            <router-link :to="{name:'CategoryVideo', query: {category:u}}"
                         style="text-decoration:none;out-line: none;color: #000">
              <img v-lazy="'../static/images/01.png'"><br>
              <span style="font-size: 12px;">{{u.cateName}}</span>
            </router-link>
          </div>
        </el-tab-pane>
      </el-tabs>
    </div>

  </div>
</template>

<script>
    import axios from 'axios'

    export default {
        name: "FenLei",
        data() {
            return {
                selected: '',
                tabPosition: 'left',
                catalogList: []
            }
        },
        methods: {
            infodata() {
                axios.get('/app/queryAllCategory').then(resp => {
                    // if (!resp.data.status) {
                    //     this.$alert(resp.data.errmsg, "提示");
                    //     return;
                    // }
                    // if (resp.data.nopermission) {
                    //     this.$alert("登录状态失效，请重新登录", "提示");
                    //     window.location.href = "/login.html";
                    //     return;
                    // }
                    this.catalogList = Object.freeze(resp.data.data);
                }).catch((err, data) => {
                    console.error(err);
                    this.$alert("请检查网络连接，如果完好请通知管理员服务器遇到错误，谢谢！", "提示");
                });
            }
        },
        created() {
            this.infodata();
        }
    }
</script>

<style scoped>
  .header {
    border: none;
    background-color: #eee;
    height: 50px;
    width: 100%;
  }

  .header div {
    text-align: center;
    line-height: 50px;
    font-size: 20px;
  }

  .catalog {
    height: 80px;
    width: 80px;
    text-align: center;
    margin-top: 10px;
    float: left;
  }


</style>



