<template>
  <div class="fullheight">
    <div class="header" style="height: 60px;text-align:center;">
      <a href="javascript:void(0)" @click="$router.go(-1)" style="text-decoration:none;out-line: none;color: #000">
        <i class='iconfont iconleft' style="font-size: 18px;line-height: 60px;margin-left: 20px;float: left;"></i>
      </a>
      <div style="font-size: 16px;width:100px;float:left;line-height: 60px;margin-left: 25%">
        {{$route.query.cellname}}
      </div>
    </div>

    <div v-for="u in favoritelist">
      <div class="categoryvideolist"
           style="height: 100px;width: 95%;margin-top: 10px;border-bottom: 1px solid #ccc;margin-left: 10px;">
        <div @click="byvideoidandcategory(u.videoid,u.category)">
          <img v-lazy="u.videoadress"
               style="width: 120px;margin-top: 10px;height: 80px;border-radius: 5px;float: left">
          <div style="margin-top: 10px;width: 160px;float: left">
            <div style="font-weight: bold;overflow: hidden;height: 20px;margin-left: 10px;font-size: 14px;">
              {{u.videoname}}
            </div>
            <br>
            <span style="font-size: 13px;color: #666">{{u.time |datetimeFormat}}</span><br>
            <span style="font-size: 12px;color: #666">{{u.username}}</span>
          </div>
        </div>
        <div style="float: right;line-height: 60px;">
          <i class='iconfont iconmsnui-more' style="float: right;margin-right: 20px;color: #666"
             @click="iconpopup(u.informationid)"></i>
        </div>
      </div>
    </div>

    <mt-popup
      v-model="popupVisible"
      style="height: 120px;width: 100%"
      position="bottom">
      <div style="height: 120px;width: 100%;">
        <div @click="unsubscribe" style="height: 60px;border-bottom: 1px solid #ccc">
          <span style="line-height: 60px;margin-left: 20px">取消收藏</span>
        </div>
        <div @click="popupVisible=false" style="height: 60px;">
          <span style="line-height: 60px;margin-left: 20px">取消</span>
        </div>
      </div>
    </mt-popup>


  </div>
</template>

<script>
    import axios from "axios"


    export default {
        name: "Favorite",
        data() {
            return {
                favoritelist: [],
                popupVisible: false,
                userid: this.$store.state.userid,
                informationid: ''
            }
        },
        methods: {
            byvideoidandcategory(videoid, category) {
                this.$router.push({name: 'Video', query: {videoid: videoid, category: category}});
            },
            iconpopup(informationid) {
                this.popupVisible = true;
                this.informationid = informationid;
            },
            unsubscribe() {
                this.popupVisible = false;
                this.$alert("获取信息表的主键，根据主键信息取消该视频文件的收藏！！！" + this.informationid);
            },
            infoDate() {
                axios.get(`/video/getByFavoriteList??userid=${this.$store.state.userid}`).then(resp => {
                    if (!resp.data.status) {
                        this.$alert(resp.data.errmsg, "提示");
                        return;
                    }
                    if (resp.data.nopermission) {
                        this.$alert("登录状态失效，请重新登录", "提示");
                        window.location.href = "/login.html";
                        return;
                    }
                    this.favoritelist = Object.freeze(resp.data.data.favoritelist)
                }).catch((err, data) => {
                    console.error(err);
                    this.$alert("请检查网络连接，如果完好请通知管理员服务器遇到错误，谢谢！", "提示");
                });
            }
        },
        created() {
            this.infoDate();
        }


    }
</script>

<style scoped>

  .fullheight {
     height: 820px;
     background-color: #eeeeee;
   }

  .categoryvideolist span {
    margin-left: 10px;
  }


</style>



