<template>
  <div class="fullheight">
    <div class="header" style="height: 60px;text-align:center;background-color: #eeeeee">
      <a href="javascript:void(0)" @click="$router.go(-1)" style="text-decoration:none;out-line: none;color: #000">
        <i class='iconfont iconleft' style="font-size: 18px;line-height: 60px;margin-left: 20px;float: left;"></i>
      </a>
      <div style="font-size: 16px;width:100px;float:left;line-height: 60px;margin-left: 25%">
        我的好友
      </div>
    </div>

    <div class="attentionDate">
      <mt-navbar v-model="selected">
        <mt-tab-item id="1">我的关注</mt-tab-item>
        <mt-tab-item id="2">我的粉丝</mt-tab-item>
      </mt-navbar>
    </div>
    <div>
      <mt-tab-container v-model="selected">
        <mt-tab-container-item id="1">
          <div v-for="(u,index) in attentionlist">
            <div style="height: 70px;" class="attentiondatecss">
              <div style="line-height: 70px;margin-left: 10px;float: left;">
                <img v-lazy="u.useradress" class="userimage">
              </div>
              <div style="float: left;height: 70px;width: 50%;margin-left: 10px;">
                <div style="margin-top: 12px;margin-bottom: 8px;">{{u.username}}</div>
                <div style="font-size: 14px;color: #666;height: 20px;overflow: hidden;">{{u.userintroduction}}</div>
              </div>
              <div style="float: right;margin-right: 10px;width: 60px;height: 70px;">
                <div>
                  <div @click="attentionclick(u,index,$event)" class="test">
                    已关注
                  </div>
                </div>
              </div>
            </div>
          </div>
        </mt-tab-container-item>
        <mt-tab-container-item id="2">
          <div v-for="(u,index) in fanlist">
            <div style="height: 70px;" class="attentiondatecss">
              <div style="line-height: 70px;margin-left: 10px;float: left;">
                <img v-lazy="u.useradress" class="userimage">
              </div>
              <div style="float: left;height: 70px;width: 50%;margin-left: 10px;">
                <div style="margin-top: 12px;margin-bottom: 8px;">{{u.username}}</div>
                <div style="font-size: 14px;color: #666;height: 20px;overflow: hidden;">{{u.userintroduction}}</div>
              </div>
              <div style="float: right;margin-right: 10px;width: 60px;height: 70px;">
                <div>
                  <div @click="fanclick(u,index,$event)" v-if="u.mutualpowder==='1'" class="test">
                    已互粉
                  </div>
                  <div @click="fanclick(u,index,$event)" v-if="u.mutualpowder==='0'" class="test1">
                    关注
                  </div>
                </div>
              </div>
            </div>
          </div>
        </mt-tab-container-item>
      </mt-tab-container>
    </div>


  </div>
</template>

<script>
    import axios from "axios"

    export default {
        name: "Attention",
        data() {
            return {
                selected: '1',
                attentionlist: [],
                fanlist:[],
                fan: [],
                val:'test',
                typicalindex:''
            }
        },
        methods: {
            attentionclick(val,index,event) {
                let dom = event.target;
                if(dom.innerHTML=="关注"){
                    this.$nextTick(() => {
                        dom.innerHTML='已关注';
                        dom.className="test";
                    });
                }else {
                    this.$nextTick(() => {
                        dom.innerHTML='关注';
                        dom.className="test1";
                    });
                }
            },
            fanclick(val,index,event) {
                let dom = event.target;
                if(dom.innerHTML=="关注"){
                    this.$nextTick(() => {
                        dom.innerHTML='已互粉';
                        dom.className="test";
                    });
                }else {
                    this.$nextTick(() => {
                        dom.innerHTML='关注';
                        dom.className="test1";
                    });
                }
            },
            infoDate() {
                axios.get(`/video/getAttentionList?userid=${this.$store.state.userid}`).then(resp => {
                    if (!resp.data.status) {
                        this.$alert(resp.data.errmsg, "提示");
                        return;
                    }
                    if (resp.data.nopermission) {
                        this.$alert("登录状态失效，请重新登录", "提示");
                        window.location.href = "/login.html";
                        return;
                    }
                    this.attentionlist = Object.freeze(resp.data.data.attentionlist);
                }).catch((err, data) => {
                    console.error(err);
                    this.$alert("请检查网络连接，如果完好请通知管理员服务器遇到错误，谢谢！", "提示");
                });

                axios.get(`/video/getFenList?attentionid=${this.$store.state.userid}`).then(resp => {
                    if (!resp.data.status) {
                        this.$alert(resp.data.errmsg, "提示");
                        return;
                    }
                    if (resp.data.nopermission) {
                        this.$alert("登录状态失效，请重新登录", "提示");
                        window.location.href = "/login.html";
                        return;
                    }
                    this.fanlist = Object.freeze(resp.data.data.fanlist);
                }).catch((err, data) => {
                    console.error(err);
                    this.$alert("请检查网络连接，如果完好请通知管理员服务器遇到错误，谢谢！", "提示");
                });
            }
        },
        created() {
            this.infoDate();
        }


    }
</script>

<style scoped>
  .userimage {
    margin: 0 auto;
    display: inline-block;
    vertical-align: middle;
    width: 45px;
    height: 45px;
    border-radius: 50%;
  }

  .attentiondatecss {
    border-bottom: 1px solid #eee;
  }

  .test {
    width: 55px;
    height: 25px;
    background-color: #ccc;
    color: #fff;
    font-size: 12px;
    text-align: center;
    line-height: 25px;
    border-radius: 2px;
    margin-top: 22.5px;
  }

  .test1 {
    width: 55px;
    height: 25px;
    background-color: #ab2e35;
    color: #fff;
    font-size: 12px;
    text-align: center;
    line-height: 25px;
    border-radius: 2px;
    margin-top: 22.5px;
  }


</style>



