<template>
  <div class="fullheight">
    <div class="header" style="height: 50px;text-align:center;">
      <a href="javascript:void(0)" @click="$router.go(-1)" style="text-decoration:none;out-line: none;color: #000">
        <i class='iconfont iconleft' style="font-size: 18px;line-height: 50px;margin-left: 20px;float: left;"></i>
      </a>
      <div style="font-size: 16px;width:100px;float:left;line-height: 50px;margin-left: 25%">
        {{$route.query.cellname}}
      </div>
      <div style="width: 80px;float: right;font-size: 16px;line-height: 50px;">
        <div v-if="show===false" @click="openHistory">
          <span>编辑</span>
        </div>
        <div v-if="show===true" @click="quxiao">
          <span>取消</span>
        </div>
      </div>
    </div>
    <div v-for="u in historylist">
      <div class="categoryvideolist"
           style="height: 100px;width: 95%;margin-top: 10px;border-bottom: 1px solid #ccc;margin-left: 10px;">
        <el-checkbox-group v-if="history" v-model="checkedCities" @change="handleCheckedCitiesChange">
          <el-checkbox :label="u.videoid" :key="u.videoid"></el-checkbox>
        </el-checkbox-group>
        <div @click="byvideoidandcategory('3247685234','Android')">
          <img v-lazy="u.videoadress"
               style="width: 120px;margin-top: 10px;height: 80px;border-radius: 5px;float: left">
          <div style="margin-top: 10px;width: 160px;float: left">
            <div style="font-weight: bold;overflow: hidden;height: 20px;margin-left: 10px;">{{u.videoname}}</div>
            <br>
            <span>{{u.time |datetimeFormat}}</span><br>
            <span>{{u.username}}</span>
          </div>
        </div>

      </div>
    </div>

    <mt-popup
      v-model="popupVisible"
      style="width: 100%"
      :modal="false"
      position="bottom">
      <div style="height: 60px;width: 100%;">
        <div @click="unsubscribe" style="">
          <div style="float: left;line-height: 60px;margin-left: 20px;">
            <el-checkbox :indeterminate="isIndeterminate" v-model="checkAll" @change="handleCheckAllChange">全选
            </el-checkbox>
          </div>
          <div @click="popupVisible=true" style="float: right;line-height: 60px;margin-right: 30px;">
            删除
          </div>
        </div>
      </div>
    </mt-popup>

  </div>
</template>

<script>
    import axios from "axios"

    let videoIdOptions = [];

    export default {
        name: "History",
        data() {
            return {
                checkAll: false,
                checkedCities: [],
                isIndeterminate: true,
                cities: videoIdOptions,
                history: false,
                popupVisible: false,
                show: false,
                historylist: []
            }
        },
        methods: {
            handleCheckedCitiesChange(value) {
                let checkedCount = value.length;
                this.checkAll = checkedCount === this.cities.length;
                this.isIndeterminate = checkedCount > 0 && checkedCount < this.cities.length;
            },
            handleCheckAllChange(val) {
                this.history = true;
                this.checkedCities = val ? videoIdOptions : [];
                this.isIndeterminate = false;
            },
            byvideoidandcategory(videoid, category) {
                this.$router.push({name: 'Video', query: {videoid: videoid, category: category}});
            },
            unsubscribe() {

            },
            openHistory() {
                this.history = true;
                this.popupVisible = true;
                this.show = true;
            },
            quxiao() {
                this.history = false;
                this.popupVisible = false;
                this.show = false;
            },
            infoDate() {
                axios.get(`/video/gethistorylist??userid=${this.$store.state.userid}`).then(resp => {
                    if (!resp.data.status) {
                        this.$alert(resp.data.errmsg, "提示");
                        return;
                    }
                    if (resp.data.nopermission) {
                        this.$alert("登录状态失效，请重新登录", "提示");
                        window.location.href = "/login.html";
                        return;
                    }
                    let list = resp.data.data.historylist;
                    for (let argument of list) {
                        videoIdOptions.push(argument.videoid);
                    }
                    this.historylist = Object.freeze(list)
                }).catch((err, data) => {
                    console.error(err);
                    this.$alert("请检查网络连接，如果完好请通知管理员服务器遇到错误，谢谢！", "提示");
                });
            }
        },
        created() {
            this.infoDate();
        }

    }
</script>

<style scoped>
  .fullheight {
    height: 820px;
    background-color: #eeeeee;
  }

  .categoryvideolist span {
    margin-left: 10px;
  }


</style>



